.specifications__name {
  font-weight: bold;
  font-size: 30px;
  color: $fonts-grey;
  margin-top: -16px; }

.specifications__list {
  display: flex;
  margin-top: 0;
  margin-left: 9px; }

.specifications__item {
  margin-right: 12px;

  @media (max-width: 1180px) {
    margin-right: 2%; } }

.specifications__type {
  text-align: center;
  font-weight: normal;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 52px;
  width: 63px;
  justify-content: space-between;
  margin-top: 0;
  opacity: 0.5; }

.specifications__price {
  font-weight: bold;
  font-size: 35px;
  color: $fonts-grey;
  margin: 0;
  margin-top: 21px;
  display: flex;
  align-items: center;

  @media (max-width: 1180px) {
    font-size: 26px; } }

.specifications__not-price {
  font-weight: normal;
  font-size: 20px;
  opacity: 0.5;
  position: relative;
  margin-left: 40px;

  @media (max-width: 1180px) {
    font-size: 16px;
    margin-left: 5%; }

  &:after {
    content: '';
    position: absolute;
    top: 14px;
    left: -7px;
    width: 115%;
    height: 1px;
    background-color: $fonts-grey;
    transform: rotate(-5deg); } }


.specifications__buttons-container {
  display: flex;
  flex-direction: column;
  margin-top: 37px; }

.specifications__button {
  margin-bottom: 20px; }
