.review-form {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(88, 87, 87, 0.6);
  display: flex;
  align-items: center; }

.review-form__container {
  box-sizing: border-box;
  width: 698px;
  height: 415px;
  background-color: $light-grey;
  margin: 0 auto;
  padding: 30px 35px 35px 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative; }

.review-form__title {
  font-weight: 600;
  font-size: 25px;
  color: $fonts-grey;
  text-align: center;
  margin: 0; }

.review-form__close-btn {
  position: absolute;
  top: 24px;
  right: 29px;
  font-size: 0;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: none;
  cursor: pointer; }

.review-form__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, auto);
  column-gap: 27px;
  row-gap: 14px;
  width: 100%;
  margin-top: 46px; }


.review-form__item {
  position: relative;
  &:last-of-type {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: -1; }

  &--stars {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    display: flex;
    align-items: center;
    justify-content: space-between; } }

.review-form__rate-name {
  font-weight: 600;
  font-size: 15px;
  color: $main-black;
  margin-top: 3px;
  margin-right: 2px; }

.review-form__label {
  &--required {
    position: relative;
    font-size: 0;
    width: 100%;

    &::before {
      content: '*';
      position: absolute;
      top: -25px;
      left: 10px;
      color: $main-red;
      font-size: 17px; } }

  &--text {
    &::before {
      top: -105px; } } }

.review-form__valid-message {
  position: absolute;
  top: -20px;
  font-weight: normal;
  font-size: 14px;
  color: $error-red; }


.review-form__input {
  padding: 15px 25px;
  font-weight: normal;
  font-size: 14px;
  font-family: 'Open Sans', 'Arial', sans-serif;
  color: $dark-grey;
  border: 1px solid rgba(72, 73, 77, 0.2);
  box-sizing: border-box;
  width: 300px;

  &--error {
    border-color: $error-red; } }

.review-form__text-area {
  box-sizing: border-box;
  width: 300px;
  height: 118px;
  resize: none;
  padding: 15px 25px;
  font-weight: normal;
  font-size: 14px;
  border: 1px solid rgba(72, 73, 77, 0.2);
  font-family: 'Open Sans', 'Arial', sans-serif;
  color: $dark-grey;

  &--error {
    border-color: $error-red; } }

.review-form__rating {
  margin-top: -10px;
  display: flex;
  flex-direction: row-reverse; }

.review-form__rating-label:not(:first-of-type) {
  margin-right: 10px; }

.review-form__btn {
  margin-top: 15px;
  padding: 17px 25px; }

.review-form__rating-label {
  cursor: pointer; }

.review-form__star-image {
  width: 27px;
  height: 27px; }

.review-form__rating-input:checked ~ .review-form__rating-label .review-form__star-image {
  fill: $main-red; }

.review-form__rating-input:hover ~ .review-form__rating-label .review-form__star-image {
  fill: $hover-red; }

